@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
div.head-container {
  position: relative;
}

div.head-container > * {
  position: absolute;
}

div.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: black;
  font-family: "Courier New", Courier, monospace;
  /* font-family: "Press Start 2P", "Courier New", Courier, monospace; */
}

html,
body,
div#root,
div.App {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
}

ul li {
  list-style: none;
}

button,
input,
textarea,
a {
  outline: none;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

button,
a,
label,
select,
option {
  cursor: pointer;
}

a {
  text-decoration: none;
}

